.greetingForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #f9f9f9;
    width: 300px;
    margin: 0 auto;
}

.inputAndButtonContainer {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-top: 10px;
}

.error {
    color: red;
    font-size: 12px;
    margin-top: 5px;
}

.input {
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 3px;
    width: 70%;
    font-size: 14px;
}

.input:focus {
    outline: none;
    border-color: #66afe9;
    box-shadow: 0 0 5px rgba(102, 175, 233, 0.5);
}

.errorInput {
    border-color: red;
}

.button {
    padding: 8px 12px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 3px;
    cursor: pointer;
    font-size: 14px;
}

.button:focus {
    outline: none;
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}

.button:active {
    background-color: #0056b3;
}

.button:disabled {
    background-color: #6c757d;
    cursor: not-allowed;
}

.text {
    font-size: 14px;
    margin-bottom: 10px;
}

.greeting {
    margin-top: 20px;
    font-size: 16px;
    color: #333;
}